.App {
  text-align: center;
}
@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.todoitemA {
  width: 200px;
  height: 30px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  color: white;
  opacity: 1;
  margin-left: 10px;
  margin-top: 5px;
  padding: 10px;
  -webkit-transform: translate(0px, 0px);
      -ms-transform: translate(0px, 0px);
          transform: translate(0px, 0px);
  -webkit-transition: height 0.1s ease, opacity 1s ease, -webkit-transform 1.5s ease;
  transition: height 0.1s ease, opacity 1s ease, -webkit-transform 1.5s ease;
  -o-transition: height 0.1s ease, transform 1.5s ease, opacity 1s ease;
  transition: height 0.1s ease, transform 1.5s ease, opacity 1s ease;
  transition: height 0.1s ease, transform 1.5s ease, opacity 1s ease, -webkit-transform 1.5s ease;
  -webkit-transition-delay: 0s;
       -o-transition-delay: 0s;
          transition-delay: 0s;
}

