.todoitem {
  width: 300px;
  height: 120px;
 background-color:lightblue;
  /*float: left;*/
  /*border: solid 2px black;*/
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  color:white;
  opacity:1;
  margin: 10px;
  padding: 5px;
  -webkit-transform: translate(0px, 0px);
      -ms-transform: translate(0px, 0px);
          transform: translate(0px, 0px);
  -webkit-transition:   opacity 3s ease, -webkit-transform 1.1s ease;
  transition:   opacity 3s ease, -webkit-transform 1.1s ease;
  -o-transition:   transform 1.1s ease, opacity 3s ease;
  transition:   transform 1.1s ease, opacity 3s ease;
  transition:   transform 1.1s ease, opacity 3s ease, -webkit-transform 1.1s ease;
  -webkit-transition-delay: 0.3s;
       -o-transition-delay: 0.3s;
          transition-delay: 0.3s;

}
.hide1 {
  width: 2px;
  height: 0px;
  background-color: lightyellow;
  border: solid 0px lightblue;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  color:white;
  opacity: 0.0;
    -webkit-transform: translate(-200px, 0px);
        -ms-transform: translate(-200px, 0px);
            transform: translate(-200px, 0px);
  -webkit-transition: height 0.5s ease, width 0.5s ease, opacity 0.5s ease,  background-color 0.5s ease,-webkit-transform 0.5s ease;
  transition: height 0.5s ease, width 0.5s ease, opacity 0.5s ease,  background-color 0.5s ease,-webkit-transform 0.5s ease;
  -o-transition: height 0.5s ease, width 0.5s ease, opacity 0.5s ease,  background-color 0.5s ease,transform 0.5s ease;
  transition: height 0.5s ease, width 0.5s ease, opacity 0.5s ease,  background-color 0.5s ease,transform 0.5s ease;
  transition: height 0.5s ease, width 0.5s ease, opacity 0.5s ease,  background-color 0.5s ease,transform 0.5s ease, -webkit-transform 0.5s ease;
  /*transition-delay: 11s;*/
}


.selected {
    color: blue;
    font-size: 12px;
    font-style: italic;
}
.current {
    /*margin-left: 20px*/
}
.levelStart {
  opacity: 1;
  -webkit-transform: translate(50px, 0px);
      -ms-transform: translate(50px, 0px);
          transform: translate(50px, 0px);
  -webkit-transition:  opacity 0.9s ease, -webkit-transform 0.9s ease;
  transition:  opacity 0.9s ease, -webkit-transform 0.9s ease;
  -o-transition:  opacity 0.9s ease, transform 0.9s ease;
  transition:  opacity 0.9s ease, transform 0.9s ease;
  transition:  opacity 0.9s ease, transform 0.9s ease, -webkit-transform 0.9s ease;
  -webkit-transition-delay: .01s;
       -o-transition-delay: .01s;
          transition-delay: .01s;

}
.levelEnd {
  -webkit-transform: translate(0px, 0px);
      -ms-transform: translate(0px, 0px);
          transform: translate(0px, 0px);
  -webkit-transition:    -webkit-transform 0.1s ease;
  transition:    -webkit-transform 0.1s ease;
  -o-transition:    transform 0.1s ease;
  transition:    transform 0.1s ease;
  transition:    transform 0.1s ease, -webkit-transform 0.1s ease;
  -webkit-transition-delay: .05s;
       -o-transition-delay: .05s;
          transition-delay: .05s;
}
.todoitem.success{
  background-color: #E8F5E9;
    border-top: solid 2px green;
    color: green
}
.todoitem.error {
  background-color:#FFEBEE;
    border-top: solid 2px red;
    color: red
}
.todoitem.warning {
    background-color: #FFFDE7;
    border-top: solid 2px #F9A825;
    color: #F9A825
}
.todoitem.info {
    background-color: #E3F2FD;
    border-top: solid 2px blue;
    color: blue
}
.example-enter {
  opacity: 0.01;
  width: 0px;
}

.example-enter.example-enter-active {
  opacity: 1;
  height: 100px;
  -webkit-transition: 700ms;
  -o-transition: 700ms;
  transition: 700ms;
}

.example-leave {
  opacity: 1;
  height: 80px;
}

.example-leave.example-leave-active {
  opacity: 0.01;
  height: 0px;
  -webkit-transition: 900ms;
  -o-transition: 900ms;
  transition: 900ms;
}



